:root {
  --dkgray: hsl(330,3%,12%);
  --ltgreen: hsl(159,92%,56%);
  --altgreen: rgb(4, 144, 95);
  --alt: hsl(0, 0%, 14%);
  --heading: "Nothing You Could Do", cursive;
  --body: "Open Sans", sans-serif;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
  background-color: var(--dkgray);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a,
a:visited,
a:link {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: var(--body);
  color: white;
}

h2 {
  font-size: 1.75rem;
}
@media (min-width: 40em) {
  h2 {
    font-size: 2rem;
  }
}
@media (min-width: 64em) {
  h2 {
    font-size: 2.25rem;
  }
}

.button {
  padding: 0.75rem;
  border: 1px solid var(--altgreen);
  font-weight: 500;
  text-transform: uppercase;
  transition: all 300ms ease-in-out;
}
.button:hover, .button:focus {
  border: 1px solid var(--ltgreen);
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
}

.hide {
  top: -3.75rem;
}
@media (min-width: 40em) {
  .hide {
    top: -4.375rem;
  }
}
@media (min-width: 64em) {
  .hide {
    top: -5rem;
  }
}

#projects,
#articles,
#contact {
  scroll-margin-top: 4.375rem;
}
@media (min-width: 40em) {
  #projects,
#articles,
#contact {
    scroll-margin-top: 5rem;
  }
}
@media (min-width: 64em) {
  #projects,
#articles,
#contact {
    scroll-margin-top: 5.625rem;
  }
}

header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 3.75rem;
  padding: 0 5% 0;
  transition: all 300ms ease;
  z-index: 99;
}
@media (min-width: 40em) {
  header {
    height: 4.375rem;
  }
}
@media (min-width: 64em) {
  header {
    height: 5rem;
    padding: 0 10% 0;
  }
}
header .overlay {
  opacity: 0;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(var(--dkgray) 66%, transparent 100%);
}
header nav {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header nav .logo {
  position: relative;
  top: 0.3125rem;
  font-family: var(--heading);
  font-weight: 400;
  font-size: 2.375rem;
}
@media (min-width: 40em) {
  header nav .logo {
    font-size: 2.625rem;
  }
}
@media (min-width: 64em) {
  header nav .logo {
    font-size: 2.875rem;
  }
}
header nav .header__toggle {
  transform: scale(1.2);
}
header nav .header__toggle > span {
  position: relative;
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: white;
  transition: all 300ms ease-in-out;
  transform-origin: 0.188rem 0.063rem;
}
header nav .header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
header nav.open .header__toggle > span {
  background-color: var(--ltgreen);
}
header nav.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
header nav.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
header nav.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
header nav .desktop-links {
  display: none;
}
@media (min-width: 64em) {
  header nav .desktop-links {
    display: block;
  }
  header nav .desktop-links a {
    margin-left: 1.25rem;
    padding: 0.1875rem 0;
    text-transform: uppercase;
    font-size: 1.125rem;
    font-weight: 500;
    position: relative;
  }
  header nav .desktop-links a::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.125rem;
    background-color: var(--ltgreen);
    opacity: 0;
    transform: scale(0);
    transform-origin: center;
    transition: opacity 300ms, transform 300ms;
  }
  header nav .desktop-links a:hover::after,
header nav .desktop-links a:focus::after {
    opacity: 1;
    transform: scale(1);
  }
}
header .mobile-menu {
  position: absolute;
  margin-top: 1.875rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.875rem;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 1.25rem;
}
header .mobile-menu__social-icons {
  display: flex;
  gap: 0.625rem;
  justify-content: space-around;
}
header .mobile-menu__social-icons img {
  width: 3.125rem;
}
@media (min-width: 64em) {
  header .mobile-menu {
    display: none;
  }
}
@media (min-width: 64em) {
  header #hamburgerButton {
    display: none;
  }
}

header::before {
  position: absolute;
  content: "";
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: var(--dkgray);
  opacity: 0.95;
}

.hero {
  height: calc(100vh - 60px);
  margin-top: 3.75rem;
  padding: 2% 5% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 40em) {
  .hero {
    height: 100%;
    margin: 4.375rem 0 8%;
    padding: 5% 10% 0;
  }
}
@media (min-width: 64em) {
  .hero {
    max-width: 62.75rem;
    margin: 5rem auto 8%;
    padding: 5vh 0;
    flex-direction: row;
    gap: 8.125rem;
  }
}
.hero__info {
  height: 100%;
  padding-top: 10%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 75% 25%;
}
@media (min-width: 40em) {
  .hero__info {
    padding-top: 5%;
    grid-template-rows: 12.8125rem 3.75rem;
  }
}
@media (min-width: 64em) {
  .hero__info {
    padding: 0;
  }
}
.hero__info--heading h2 {
  margin-bottom: 0.625rem;
  font-size: 2.125rem;
}
@media (min-width: 40em) {
  .hero__info--heading h2 {
    margin-bottom: 1.25rem;
    font-size: 2.375rem;
  }
}
@media (min-width: 64em) {
  .hero__info--heading h2 {
    font-size: 2.625rem;
  }
}
.hero__info--heading p {
  margin-bottom: 0.625rem;
  font-size: 1.25rem;
  line-height: 1.5;
}
.hero__info--heading p span {
  color: var(--ltgreen);
  font-weight: 500;
}
.hero__info--buttons {
  display: flex;
  align-self: center;
  flex-wrap: wrap;
  gap: 0.3125rem;
}
@media (min-width: 40em) {
  .hero__info--buttons a {
    margin-right: 0.3125rem;
  }
}
@media (min-width: 64em) {
  .hero__info--buttons {
    align-self: end;
  }
}
.hero__image {
  width: 20rem;
  height: 20rem;
  order: -1;
  opacity: 0.9;
  aspect-ratio: 1/1;
}
@media (min-width: 40em) {
  .hero__image {
    width: 21.875rem;
    height: 21.875rem;
  }
}
@media (min-width: 64em) {
  .hero__image {
    order: 0;
  }
}
.hero__image img {
  height: 100%;
  width: 100%;
}

.skills {
  max-width: 62.75rem;
  margin: 1.25rem auto 3.75rem;
  padding: 0 1.25rem;
  text-align: center;
}
@media (min-width: 40em) {
  .skills {
    margin-bottom: 5rem;
  }
}
.skills h2 {
  margin-bottom: 1.25rem;
}
.skills__icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .skills__icons {
    gap: 0.9375rem;
  }
}
.skills__icons--underline {
  width: 5rem;
  height: 5rem;
  position: relative;
}
.skills__icons--underline img {
  width: 4.0625rem;
  height: 4.0625rem;
  transform: scale(1);
  transition: transform 300ms;
}
@media (min-width: 40em) {
  .skills__icons--underline {
    width: 4.6875rem;
    height: 4.6875rem;
  }
}
.skills__icons--underline img:hover {
  transform: scale(0.95);
}
.skills__icons--underline::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--ltgreen);
  opacity: 0;
  transform: scale(0);
  transform-origin: center;
  transition: opacity 300ms, transform 300ms;
}
.skills__icons--underline:hover::after, .skills__icons--unerline:focus::after {
  opacity: 1;
  transform: scale(1);
}

#projects {
  margin: 1.25rem 0 5rem;
}
#projects h2 {
  margin-bottom: 1.25rem;
  text-align: center;
}
#projects .projects-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
  flex-direction: column;
}
@media (min-width: 64em) {
  #projects .projects-container {
    flex-direction: row;
  }
}
#projects .project__card {
  position: relative;
  width: 90vw;
  height: 10.3125rem;
  padding: 0.9375rem 1.25rem 0.5rem;
  display: grid;
  grid-template-columns: 1fr;
  background: linear-gradient(to right, var(--dkgray), var(--alt));
  border-left: 2px solid var(--altgreen);
  border-radius: 0.3125rem;
  box-shadow: -1px 15px 30px -12px rgba(0, 0, 0, 0.5);
  transition: all 300ms ease-in;
}
@media (min-width: 40em) {
  #projects .project__card {
    grid-template-rows: 2.1875rem repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  #projects .project__card {
    max-width: 30vw;
    grid-template-rows: 3.4375rem repeat(2, 1fr);
  }
}
@media (min-width: 87.5em) {
  #projects .project__card {
    max-width: 27.5rem;
  }
}
#projects .project__card h3 {
  position: relative;
  margin-bottom: 0.625rem;
  grid-row: 1/2;
}
@media (min-width: 40em) {
  #projects .project__card h3 {
    font-size: 1.25rem;
  }
}
#projects .project__card p {
  position: relative;
  margin-bottom: 1.25rem;
  grid-row: 2/3;
  color: var(--ltgreen);
  opacity: 0.8;
}
#projects .project__card--bottom-row {
  display: flex;
  justify-content: space-between;
  align-self: end;
  grid-row: 3/4;
}
#projects .project__card--bottom-row .tech {
  display: flex;
  gap: 0.4375rem;
}
#projects .project__card--bottom-row .tech img {
  position: relative;
  width: 1.125rem;
  height: 100%;
}
#projects .project__card--bottom-row .project-buttons {
  display: flex;
  gap: 0.75rem;
}
@media (min-width: 40em) {
  #projects .project__card--bottom-row .project-buttons {
    gap: 0.9375rem;
  }
}
@media (min-width: 64em) {
  #projects .project__card--bottom-row .project-buttons {
    gap: 0.75rem;
  }
}
@media (min-width: 87.5em) {
  #projects .project__card--bottom-row .project-buttons {
    gap: 0.9375rem;
  }
}
#projects .project__card--bottom-row .project-buttons a {
  position: relative;
  font-size: 0.9375rem;
  color: rgba(255, 255, 255, 0.7);
  border-bottom: 2px solid var(--altgreen);
  cursor: pointer;
  z-index: 1;
  transition: color 300ms;
}
@media (min-width: 40em) {
  #projects .project__card--bottom-row .project-buttons a {
    font-size: 1rem;
  }
}
#projects .project__card--bottom-row .project-buttons a:hover,
#projects .project__card--bottom-row .project-buttons a:focus {
  color: white;
}
#projects .project__card--bottom-row .project-buttons a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--ltgreen);
  opacity: 0;
  transform: scale(0);
  transform-origin: center;
  transition: opacity 300ms, transform 300ms;
}
#projects .project__card--bottom-row .project-buttons a:hover::after,
#projects .project__card--bottom-row .project-buttons a:focus::after {
  opacity: 1;
  transform: scale(1);
}
#projects .project__card:hover, #projects .project__card:focus {
  border-left: 2px solid var(--ltgreen);
  box-shadow: -1px 15px 30px -12px rgba(0, 0, 0, 0.9);
}
#projects .project__card::after {
  position: absolute;
  content: "";
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 2px;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: opacity 300ms ease-in;
}
#projects .project__card:hover::after, #projects .project__card:focus::after {
  opacity: 0.15;
}
#projects .project__direction {
  margin-top: 0.9375rem;
  padding: 0 2.5rem;
  text-align: center;
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.3);
}

#articles {
  margin: 1.25rem 0 5rem;
  text-align: center;
}
#articles h2 {
  margin-bottom: 1.25rem;
  flex: 0 0 100%;
}
#articles .articles-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
  flex-wrap: wrap;
}
#articles .article {
  position: relative;
  width: 21.875rem;
  height: 20rem;
  margin: 0 auto;
  padding: 0.625rem 1.25rem 0.3125rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 10% 30% 10% 50%;
  align-items: center;
  background: linear-gradient(to top, var(--dkgray), var(--alt));
  border-radius: 0.3125rem;
  box-shadow: -1px 15px 30px -12px rgba(0, 0, 0, 0.5);
  transition: all 300ms;
}
#articles .article__category {
  grid-row: 1/2;
  color: var(--ltgreen);
}
#articles .article h3 {
  grid-row: 2/3;
  text-align: left;
}
#articles .article__desc {
  grid-row: 3/4;
  text-align: left;
  opacity: 0.8;
}
#articles .article__img {
  width: 100%;
  height: 7.8125rem;
  grid-row: 4/5;
  background-position: center;
  background-size: cover;
  align-self: bottom;
  opacity: 0.5;
}
#articles .article:hover,
#articles .article:focus {
  box-shadow: -1px 15px 30px -12px rgba(0, 0, 0, 0.8);
  transform: scale(1.02);
}
#articles .article::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--ltgreen);
  opacity: 0;
  transform: scale(0);
  transform-origin: left;
  transition: all 500ms;
}
#articles .article:hover::after,
#articles .article:focus::after {
  opacity: 1;
  transform: scale(1);
}

#contact {
  margin: 1.25rem 0 5rem;
}
#contact h2 {
  margin-bottom: 1.25rem;
  text-align: center;
}
#contact .contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2.5rem;
}
@media (min-width: 40em) {
  #contact .contact {
    max-width: 62.5rem;
    margin: 0 auto;
    padding: 0 1.25rem;
    flex-direction: row;
    justify-content: space-around;
  }
}
#contact .contact p {
  font-size: 0.875rem;
  text-align: center;
  opacity: 0.8;
}
#contact .contact__form {
  width: 90vw;
  padding: 0.625rem 0.625rem 1.25rem;
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 40em) {
  #contact .contact__form {
    flex: 1 1 50%;
  }
}
#contact .contact__form--input {
  width: 100%;
  margin: 0.625rem 0 1.875rem;
  padding: 0.9375rem 0 0;
  display: flex;
  flex-direction: column;
}
#contact .contact__form--input input,
#contact .contact__form--input textarea {
  width: 100%;
  min-width: 100%;
  padding: 0.5rem 0;
  border: none;
  border-bottom: 2px solid var(--altgreen);
  background: transparent;
  outline: 0;
  font-family: inherit;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  transition: border 300ms;
}
#contact .contact__form--input textarea {
  resize: vertical;
}
#contact .contact__form--input input:focus,
#contact .contact__form--input textarea:focus {
  border-bottom: 2px solid var(--ltgreen);
}
#contact .contact__form--input input:-webkit-autofill,
#contact .contact__form--input input:-webkit-autofill:hover,
#contact .contact__form--input input:-webkit-autofill:focus,
#contact .contact__form--input textarea:-webkit-autofill,
#contact .contact__form--input textarea:-webkit-autofill:hover,
#contact .contact__form--input textarea:-webkit-autofill:focus,
#contact .contact__form--input select:-webkit-autofill,
#contact .contact__form--input select:-webkit-autofill:hover,
#contact .contact__form--input select:-webkit-autofill:focus {
  border: 1px solid transparent;
  -webkit-text-fill-color: rgba(255, 255, 255, 0.9);
  -webkit-box-shadow: 0 0 0px 1000px var(--alt) inset;
  transition: background-color 5000s ease-in-out 0s;
}
#contact .contact__form button {
  width: 30%;
  padding: 0.625rem;
  background: transparent;
  font-family: inherit;
  font-size: 1rem;
  color: inherit;
  cursor: pointer;
}
#contact .contact__info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 40em) {
  #contact .contact__info {
    flex: 1 1 50%;
  }
}
#contact .contact__info #workEmail {
  margin-bottom: 2.5rem;
}
#contact .contact__info p {
  margin-bottom: 0.625rem;
}
#contact .contact__info img {
  width: 3.125rem;
  border-radius: 50%;
  transition: 300ms ease-in-out;
}
#contact .contact__info img:hover {
  background-color: var(--ltgreen);
}

footer {
  height: 4.6875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--alt);
  text-align: center;
  font-size: 0.8125rem;
  color: rgba(255, 255, 255, 0.6);
}

.article-container {
  width: 90vw;
  max-width: 50rem;
  margin: 0 auto;
}
@media (min-width: 87.5em) {
  .article-container {
    max-width: 75rem;
  }
}
.article-container a {
  color: var(--ltgreen);
  opacity: 0.9;
  transition: 300ms;
}
.article-container a:hover,
.article-container a:focus {
  opacity: 0.5;
}
.article-container .back-to-main {
  margin: 2.5rem auto;
  color: var(--ltgreen);
  opacity: 0.9;
}
.article-container .article {
  padding: 2.5rem 0.9375rem 0.625rem;
  display: flex;
  flex-direction: column;
  border-top: 2px solid var(--ltgreen);
  border-right: 2px solid var(--alt);
  border-bottom: 2px solid var(--alt);
  border-left: 2px solid var(--alt);
  border-radius: 8px 8px 0 0;
  text-align: left;
  box-shadow: -1px 15px 30px -12px rgba(0, 0, 0, 0.5);
}
@media (min-width: 40em) {
  .article-container .article {
    padding: 2.5rem 1.875rem 0.625rem;
  }
}
@media (min-width: 87.5em) {
  .article-container .article {
    padding: 3.125rem 3.125rem 0.625rem;
  }
}
.article-container .article .category {
  margin-bottom: 0.0625rem;
  letter-spacing: 0.1875rem;
  text-transform: uppercase;
  color: var(--ltgreen);
  font-size: 0.9375rem;
}
@media (min-width: 40em) {
  .article-container .article .category {
    font-size: 1.125rem;
  }
}
.article-container .article .estimation {
  margin-bottom: 2.5rem;
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.6);
}
@media (min-width: 40em) {
  .article-container .article .estimation {
    font-size: 1rem;
  }
}
.article-container .article__img {
  margin: 0 0 2.5rem;
  padding-bottom: 60%;
  background-position: center;
  background-size: cover;
  opacity: 0.6;
}
@media (min-width: 40em) {
  .article-container .article__img {
    padding-bottom: 50%;
  }
}
@media (min-width: 87.5em) {
  .article-container .article__img {
    padding-bottom: 45%;
  }
}
.article-container .article__index {
  margin-bottom: 1.25rem;
}
.article-container .article__index ul {
  margin-left: 1.25rem;
  list-style-type: none;
}
.article-container .article__index ul li {
  margin-bottom: 0.625rem;
  line-height: 1.7;
}
@media (min-width: 40em) {
  .article-container .article__index ul li {
    font-size: 1.125rem;
  }
}
.article-container .article h1 {
  margin-bottom: 3.125rem;
  font-size: 2.25rem;
  line-height: 1;
}
@media (min-width: 40em) {
